.discover-container {
  background: linear-gradient(135deg, #0a1446 0%, #1a237e 60%, #3f51b5 100%);
  font-family: 'Segoe UI', 'PingFang SC', 'Hiragino Sans GB', 'Arial', sans-serif;
  min-height: 100vh;
  padding: 24px 0 40px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 32px;
}
.discover-section {
  background: rgba(30, 40, 90, 0.7);
  border-radius: 20px;
  box-shadow: 0 8px 32px rgba(10,20,70,0.18);
  padding: 36px 32px 28px 32px;
  max-width: 800px;
  width: 98vw;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 18px;
  color: #fff;
}
.discover-title {
  font-size: 1.5rem;
  font-weight: bold;
  color: #42a5f5;
  margin-bottom: 12px;
}
.discover-content-placeholder {
  min-height: 60px;
  background: rgba(66, 165, 245, 0.08);
  border-radius: 14px;
  padding: 18px 16px;
  color: #b6cfff;
  font-size: 1.08rem;
  width: 100%;
  display: flex;
  align-items: center;
}

.discover-flowchart {
  background: #fff;
  border-radius: 20px;
  box-shadow: 0 4px 24px rgba(0,0,0,0.10);
  padding: 32px 24px;
  max-width: 760px;
  width: 98vw;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
}

.discover-steps {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 32px;
  gap: 0;
}
.discover-step {
  display: flex;
  align-items: center;
  background: #e3eafc;
  color: #2563eb;
  border-radius: 16px;
  padding: 12px 24px;
  font-size: 1.1rem;
  font-weight: 500;
  margin: 0 4px;
  transition: background 0.2s, color 0.2s;
}
.discover-step.active {
  background: #2563eb;
  color: #fff;
  font-weight: bold;
}
.discover-step-arrow {
  margin: 0 8px;
  font-size: 1.5rem;
  color: #b6cfff;
}
.discover-step-card {
  background: #fff;
  border-radius: 20px;
  box-shadow: 0 4px 24px rgba(0,0,0,0.10);
  padding: 36px 32px 28px 32px;
  max-width: 480px;
  margin: 0 auto;
  text-align: center;
}
.discover-step-title {
  font-size: 1.3rem;
  font-weight: bold;
  color: #2563eb;
  margin-bottom: 12px;
}
.discover-step-desc {
  color: #374151;
  font-size: 1.08rem;
  margin-bottom: 24px;
}
.discover-step-actions {
  display: flex;
  justify-content: center;
  gap: 18px;
}
.discover-step-btn {
  background: #2563eb;
  color: #fff;
  border: none;
  border-radius: 10px;
  padding: 10px 32px;
  font-size: 1.08rem;
  font-weight: 600;
  cursor: pointer;
  transition: background 0.2s;
}
.discover-step-btn:disabled {
  background: #b6cfff;
  color: #fff;
  cursor: not-allowed;
}
.discover-step-btn:hover:not(:disabled) {
  background: #003580;
}

.discover-form-fields {
  display: flex;
  flex-direction: column;
  gap: 16px;
  margin-bottom: 18px;
  align-items: stretch;
}

.discover-form-label {
  font-size: 1.05rem;
  color: #374151;
  font-weight: 500;
  display: flex;
  flex-direction: column;
  gap: 6px;
}

.discover-form-input {
  border: 1px solid #e3eafc;
  background: #f9fafb;
  border-radius: 8px;
  padding: 10px 12px;
  font-size: 1.05rem;
  color: #111418;
  outline: none;
  transition: border 0.2s;
}
.discover-form-input:focus {
  border: 1.5px solid #2563eb;
  background: #fff;
}

.discover-finish-msg {
  color: #2563eb;
  font-size: 1.15rem;
  font-weight: bold;
  text-align: center;
  margin: 24px 0 0 0;
}

.announcements-block {
  margin-bottom: 24px;
}
.announcements-title {
  font-size: 1.15rem;
  font-weight: 600;
  color: #42a5f5;
  margin-bottom: 10px;
}
.announcements-list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  gap: 14px;
}
.announcement-item {
  background: rgba(66, 165, 245, 0.10);
  border-radius: 12px;
  padding: 14px 18px;
  box-shadow: 0 2px 8px rgba(33,150,243,0.08);
  display: flex;
  flex-direction: column;
  gap: 6px;
}
.announcement-date {
  color: #8ec6ff;
  font-size: 0.98rem;
  font-weight: 500;
}
.announcement-title {
  color: #42a5f5;
  font-size: 1.08rem;
  font-weight: 600;
}
.news-block {
  margin-top: 18px;
}
.news-title {
  font-size: 1.15rem;
  font-weight: 600;
  color: #42a5f5;
  margin-bottom: 10px;
}
.news-list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.news-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: rgba(66, 165, 245, 0.06);
  border-radius: 8px;
  padding: 10px 14px;
  font-size: 1.05rem;
}
.news-item-title {
  color: #e3eafc;
  font-weight: 500;
}
.news-item-date {
  color: #8ec6ff;
  font-size: 0.98rem;
  margin-left: 16px;
  min-width: 90px;
  text-align: right;
}

.discover-sub-block {
  width: 100%;
  margin-bottom: 18px;
}
.discover-sub-title {
  font-size: 1.12rem;
  font-weight: 600;
  color: #8ec6ff;
  margin-bottom: 8px;
  letter-spacing: 1px;
}

@media (max-width: 800px) {
  .discover-flowchart {
    padding: 18px 2vw;
    max-width: 98vw;
  }
  .discover-section {
    padding: 18px 2vw 16px 2vw;
    max-width: 98vw;
  }
  .discover-title {
    font-size: 1.1rem;
  }
} 