.me-container {
  background: linear-gradient(135deg, #0a1446 0%, #1a237e 60%, #3f51b5 100%);
  font-family: 'Segoe UI', 'PingFang SC', 'Hiragino Sans GB', 'Arial', sans-serif;
  min-height: 100vh;
  padding: 24px 0 40px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 32px;
}
.me-section {
  background: linear-gradient(135deg, #0a1446 0%, #1a237e 60%, #3f51b5 100%);
  border-radius: 20px;
  box-shadow: 0 8px 32px rgba(10,20,70,0.18);
  padding: 36px 32px 28px 32px;
  max-width: 800px;
  width: 98vw;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 18px;
  color: #fff;
}
.me-title {
  font-size: 1.5rem;
  font-weight: bold;
  color: #42a5f5;
  margin-bottom: 12px;
}
.me-content-placeholder {
  min-height: 60px;
  background: rgba(66, 165, 245, 0.08);
  border-radius: 14px;
  padding: 18px 16px;
  color: #b6cfff;
  font-size: 1.08rem;
  width: 100%;
  display: flex;
  align-items: center;
}
.me-sub-block {
  width: 100%;
  margin-bottom: 18px;
}
.me-sub-title {
  font-size: 1.12rem;
  font-weight: 600;
  color: #8ec6ff;
  margin-bottom: 8px;
  letter-spacing: 1px;
}
.me-setting-row {
  display: flex;
  align-items: center;
  gap: 18px;
  margin-bottom: 12px;
}
.me-switch {
  position: relative;
  display: inline-block;
  width: 44px;
  height: 24px;
}
.me-switch input {
  opacity: 0;
  width: 0;
  height: 0;
}
.me-slider {
  position: absolute;
  cursor: pointer;
  top: 0; left: 0; right: 0; bottom: 0;
  background: #b6cfff;
  border-radius: 24px;
  transition: .3s;
}
.me-switch input:checked + .me-slider {
  background: #42a5f5;
}
.me-slider:before {
  position: absolute;
  content: '';
  height: 18px;
  width: 18px;
  left: 3px;
  bottom: 3px;
  background: #fff;
  border-radius: 50%;
  transition: .3s;
}
.me-switch input:checked + .me-slider:before {
  transform: translateX(20px);
}
.me-setting-status {
  color: #8ec6ff;
  font-size: 1.02rem;
  min-width: 48px;
}
.me-lang-select {
  background: #1a237e;
  color: #fff;
  border: 1px solid #42a5f5;
  border-radius: 8px;
  padding: 6px 14px;
  font-size: 1.05rem;
}
.me-password-input {
  background: #1a237e;
  color: #fff;
  border: 1px solid #42a5f5;
  border-radius: 8px;
  padding: 6px 12px;
  font-size: 1.05rem;
}
.me-setting-btn {
  background: #42a5f5;
  color: #fff;
  border: none;
  border-radius: 8px;
  padding: 6px 18px;
  font-size: 1.05rem;
  font-weight: 600;
  cursor: pointer;
  transition: background 0.2s;
}
.me-setting-btn:disabled {
  background: #b6cfff;
  color: #fff;
  cursor: not-allowed;
}
@media (max-width: 900px) {
  .me-section {
    max-width: 98vw;
    padding: 0 2vw 24px 2vw;
  }
} 