.performance-container {
  background: linear-gradient(135deg, #0a1446 0%, #1a237e 60%, #3f51b5 100%);
  font-family: 'Segoe UI', 'PingFang SC', 'Hiragino Sans GB', 'Arial', sans-serif;
  padding: 24px 0 40px 0;
  min-height: 100vh;
}
.performance-section {
  background: rgba(30, 40, 90, 0.7);
  border-radius: 20px;
  box-shadow: 0 8px 32px rgba(10,20,70,0.18);
  padding: 36px 32px 28px 32px;
  max-width: 600px;
  margin: 0 auto 28px auto;
  display: flex;
  flex-direction: column;
  gap: 18px;
  color: #fff;
}
.performance-title {
  font-size: 1.25rem;
  font-weight: bold;
  color: #42a5f5;
  margin-bottom: 10px;
}
.performance-content-placeholder {
  min-height: 60px;
  background: rgba(66, 165, 245, 0.10);
  border-radius: 14px;
  padding: 18px 16px;
  color: #b6cfff;
  font-size: 1.08rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
.kpi-assign-block {
  display: flex;
  gap: 32px;
  flex-wrap: wrap;
  justify-content: space-between;
}
.kpi-list-block, .kpi-claimed-block {
  flex: 1 1 260px;
  min-width: 240px;
  background: rgba(66, 165, 245, 0.10);
  border-radius: 14px;
  padding: 18px 16px;
  display: flex;
  flex-direction: column;
  gap: 12px;
}
.kpi-list-title {
  font-size: 1.08rem;
  font-weight: 600;
  color: #8ec6ff;
  margin-bottom: 8px;
}
.kpi-list, .kpi-claimed-list {
  list-style: none;
  padding: 0;
  margin: 0 0 12px 0;
  display: flex;
  flex-direction: column;
  gap: 8px;
}
.kpi-list-item, .kpi-claimed-item {
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 1rem;
  color: #42a5f5;
  background: rgba(66, 165, 245, 0.08);
  border-radius: 8px;
  padding: 6px 10px;
}
.kpi-list-item input[type="checkbox"] {
  accent-color: #42a5f5;
  margin-right: 6px;
}
.kpi-label {
  flex: 1;
  color: #42a5f5;
}
.kpi-claim-btn {
  background: linear-gradient(90deg, #2196f3 0%, #1976d2 100%);
  color: #fff;
  border: none;
  border-radius: 18px;
  padding: 8px 24px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: background 0.2s, color 0.2s, box-shadow 0.2s;
  box-shadow: 0 2px 8px rgba(33,150,243,0.15);
  margin-top: 8px;
}
.kpi-claim-btn:disabled {
  background: #b6cfff;
  color: #fff;
  cursor: not-allowed;
}
.kpi-claimed-item {
  background: rgba(34, 197, 94, 0.10);
  color: #22c55e;
  position: relative;
}
.kpi-remove-btn {
  background: none;
  border: none;
  color: #ef4444;
  font-size: 1.2rem;
  font-weight: bold;
  cursor: pointer;
  margin-left: 8px;
  border-radius: 50%;
  width: 24px;
  height: 24px;
  line-height: 20px;
  text-align: center;
  transition: background 0.2s;
}
.kpi-remove-btn:hover {
  background: rgba(239,68,68,0.12);
}
.kpi-achievement-block {
  display: flex;
  flex-direction: column;
  gap: 28px;
}
.kpi-charts-row {
  display: flex;
  gap: 32px;
  flex-wrap: wrap;
  justify-content: space-between;
}
.kpi-chart-block {
  flex: 1 1 320px;
  min-width: 280px;
  background: rgba(66, 165, 245, 0.10);
  border-radius: 14px;
  padding: 18px 16px;
  display: flex;
  flex-direction: column;
  align-items: center;
  box-shadow: 0 2px 8px rgba(33,150,243,0.10);
}
.kpi-chart-title {
  font-size: 1.08rem;
  font-weight: 600;
  color: #8ec6ff;
  margin-bottom: 8px;
}
.kpi-line-chart, .kpi-bar-chart {
  width: 100%;
  max-width: 320px;
  height: 120px;
  margin-top: 8px;
}
.kpi-warning-block {
  background: rgba(239,68,68,0.10);
  border-radius: 14px;
  padding: 18px 16px;
  color: #ef4444;
  box-shadow: 0 2px 8px rgba(239,68,68,0.08);
}
.kpi-warning-title {
  font-size: 1.08rem;
  font-weight: 600;
  margin-bottom: 8px;
}
.kpi-warning-list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  gap: 6px;
}
.kpi-warning-item {
  font-size: 1rem;
  color: #ef4444;
  background: rgba(239,68,68,0.08);
  border-radius: 8px;
  padding: 6px 10px;
  font-weight: 500;
}
.growth-metrics-block {
  display: flex;
  flex-direction: column;
  gap: 24px;
}
.growth-metric-item {
  background: rgba(66, 165, 245, 0.10);
  border-radius: 14px;
  padding: 18px 16px;
  display: flex;
  align-items: center;
  gap: 18px;
  box-shadow: 0 2px 8px rgba(33,150,243,0.10);
}
.growth-metric-title {
  flex: 0 0 160px;
  font-size: 1.08rem;
  font-weight: 600;
  color: #8ec6ff;
}
.growth-progress-bar-bg {
  flex: 1;
  background: #e3eafc;
  border-radius: 8px;
  height: 14px;
  margin: 0 16px;
  position: relative;
}
.growth-progress-bar {
  background: #42a5f5;
  border-radius: 8px;
  height: 14px;
  position: absolute;
  left: 0;
  top: 0;
  transition: width 0.4s;
}
.growth-progress-text {
  font-size: 1.08rem;
  font-weight: bold;
  color: #42a5f5;
  min-width: 48px;
  text-align: right;
}
@media (max-width: 900px) {
  .kpi-assign-block {
    flex-direction: column;
    gap: 18px;
  }
  .kpi-charts-row {
    flex-direction: column;
    gap: 18px;
  }
}
@media (max-width: 700px) {
  .growth-metric-item {
    flex-direction: column;
    align-items: flex-start;
    gap: 8px;
  }
  .growth-progress-bar-bg {
    margin: 0 0 8px 0;
    width: 100%;
  }
  .growth-progress-text {
    min-width: 0;
  }
} 