.process-container {
  background: linear-gradient(135deg, #0a1446 0%, #1a237e 60%, #3f51b5 100%);
  font-family: 'Segoe UI', 'PingFang SC', 'Hiragino Sans GB', 'Arial', sans-serif;
  min-height: 100vh;
  padding: 24px 0 40px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 32px;
}
.process-section {
  background: rgba(30, 40, 90, 0.7);
  border-radius: 20px;
  box-shadow: 0 8px 32px rgba(10,20,70,0.18);
  padding: 36px 32px 28px 32px;
  max-width: 800px;
  width: 98vw;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 18px;
  color: #fff;
}
.process-title {
  font-size: 1.5rem;
  font-weight: bold;
  color: #42a5f5;
  margin-bottom: 12px;
}
.process-content-placeholder {
  min-height: 60px;
  background: rgba(66, 165, 245, 0.08);
  border-radius: 14px;
  padding: 18px 16px;
  color: #b6cfff;
  font-size: 1.08rem;
  width: 100%;
  display: flex;
  align-items: center;
}

.process-steps {
  display: flex;
  justify-content: space-between;
  list-style: none;
  padding: 0;
  margin: 0 0 18px 0;
}

.process-step {
  flex: 1;
  text-align: center;
  position: relative;
  font-size: 1.05rem;
  color: #b6cfff;
  font-weight: 500;
  padding-bottom: 8px;
}
.process-step.active {
  color: #42a5f5;
  font-weight: bold;
}
.process-step:not(:last-child)::after {
  content: '';
  position: absolute;
  right: -50%;
  top: 50%;
  width: 100%;
  height: 3px;
  background: #22306b;
  z-index: 0;
}
.process-step.active:not(:last-child)::after {
  background: #42a5f5;
}

.process-desc {
  color: #e3eafc;
  font-size: 1.08rem;
  text-align: left;
  margin-bottom: 8px;
}

.process-contacts {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.process-contact-card {
  display: flex;
  align-items: center;
  background: rgba(66,165,245,0.10);
  border-radius: 12px;
  padding: 14px 12px;
  box-shadow: 0 2px 8px rgba(33,150,243,0.10);
}

.process-contact-avatar {
  width: 56px;
  height: 56px;
  border-radius: 50%;
  object-fit: cover;
  margin-right: 16px;
  border: 2px solid #42a5f5;
}

.process-contact-name {
  font-weight: 600;
  color: #fff;
}

.process-contact-role {
  font-size: 0.98rem;
  color: #b6cfff;
}

.process-contact-email {
  font-size: 0.97rem;
  color: #42a5f5;
}

.process-sub-block {
  width: 100%;
  margin-bottom: 18px;
}
.process-sub-title {
  font-size: 1.12rem;
  font-weight: 600;
  color: #8ec6ff;
  margin-bottom: 8px;
  letter-spacing: 1px;
}
.process-steps-list {
  display: flex;
  flex-wrap: wrap;
  gap: 14px;
  margin-bottom: 18px;
}
.process-step-btn {
  background: rgba(66, 165, 245, 0.10);
  color: #42a5f5;
  border: 1.5px solid #42a5f5;
  border-radius: 12px;
  padding: 10px 22px;
  font-size: 1.05rem;
  font-weight: 500;
  cursor: pointer;
  transition: background 0.2s, color 0.2s, border 0.2s;
}
.process-step-btn.active, .process-step-btn:hover {
  background: #42a5f5;
  color: #fff;
  border-color: #42a5f5;
}
.process-diagram-block {
  width: 100%;
  min-height: 90px;
  background: rgba(66, 165, 245, 0.08);
  border-radius: 14px;
  padding: 18px 16px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 8px;
}

.process-faq-block {
  width: 100%;
  margin-bottom: 12px;
}
.process-faq-title {
  font-size: 1.08rem;
  font-weight: 600;
  color: #8ec6ff;
  margin-bottom: 6px;
  letter-spacing: 1px;
}
.process-faq-link-card {
  display: block;
  text-decoration: none;
  background: rgba(66, 165, 245, 0.13);
  border-radius: 14px;
  box-shadow: 0 2px 8px rgba(66,165,245,0.08);
  transition: background 0.2s, box-shadow 0.2s;
  margin-bottom: 8px;
}
.process-faq-link-card:hover {
  background: #42a5f5;
  box-shadow: 0 4px 16px rgba(66,165,245,0.18);
}
.process-faq-link-card:hover .process-faq-title,
.process-faq-link-card:hover .process-content-placeholder {
  color: #fff;
} 